webpackJsonp([67384365728920],{

/***/ 335:
/***/ (function(module, exports) {

	module.exports = {"data":{"markdownRemark":{"html":"<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 740px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 39.473684210526315%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Local development with React and Webpack\"\n        title=\"\"\n        src=\"/static/tiny-react-env-header-8829ed1bc0cad5c4fc58d47e4db3017b-1de49.png\"\n        srcset=\"/static/tiny-react-env-header-8829ed1bc0cad5c4fc58d47e4db3017b-786d0.png 185w,\n/static/tiny-react-env-header-8829ed1bc0cad5c4fc58d47e4db3017b-055ad.png 370w,\n/static/tiny-react-env-header-8829ed1bc0cad5c4fc58d47e4db3017b-1de49.png 740w,\n/static/tiny-react-env-header-8829ed1bc0cad5c4fc58d47e4db3017b-fdb65.png 760w\"\n        sizes=\"(max-width: 740px) 100vw, 740px\"\n      />\n    </span>\n  </span>\n  </p>\n<p>React has become an industry standard at this point. It's the first JavaScript framework that a lot of new developers will learn and the default option for many other experienced ones. </p>\n<p>It's also easy to get familiar with. A vibrant community has produced tons of material on how to get up and running. Beginners can write and display their first component within minutes by using a tool like <a href=\"https://codesandbox.io/\">CodeSandbox</a>.</p>\n<p>In this article, we'll take a look at the next step in the process: <strong>local development</strong>. We'll take a stab at building our own React development environment and, by the end, we should have a setup that:</p>\n<ol>\n<li>Compiles <a href=\"https://reactjs.org/docs/jsx-in-depth.html\">JSX</a> into JS, Sass into CSS, and outputs them as a single file (bundle).</li>\n<li>Makes sure our code runs in any modern browser.</li>\n<li>Allows us to locally preview changes through hot reloading.</li>\n<li>Is ready to deploy with <a href=\"http://surge.sh/\">Surge</a>.</li>\n</ol>\n<blockquote>\n<p>If you want to follow along, all the code presented here can also be found at <a href=\"https://github.com/RadValentin/tiny-react-env\">github.com/RadValentin/tiny-react-env</a></p>\n</blockquote>\n<h2 id=\"creating-a-react-bundle\"><a href=\"#creating-a-react-bundle\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Creating a React Bundle</h2>\n<p>Let's create a folder to house our future project and initialize it with a <code class=\"language-text\">package.json</code> file. We'll also add React, since we know we're going to need it later.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-sh\"><code class=\"language-sh\">mkdir tiny-react-env\ncd tiny-react-env\n\n# this will ask you a bunch of questions\n# enter as much or as little info as you want\nnpm init\n\nnpm install --save react\nnpm install --save react-dom</code></pre>\n      </div>\n<p>We'll also need some sort of component â€” let's add it in<code class=\"language-text\">src/components/App.js</code>.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-jsx\"><code class=\"language-jsx\"><span class=\"token comment\">/* src/components/App.js */</span>\n<span class=\"token keyword\">import</span> React<span class=\"token punctuation\">,</span> <span class=\"token punctuation\">{</span>Component<span class=\"token punctuation\">}</span> <span class=\"token keyword\">from</span> <span class=\"token string\">'react'</span><span class=\"token punctuation\">;</span>\n<span class=\"token keyword\">import</span> ReactDOM <span class=\"token keyword\">from</span> <span class=\"token string\">'react-dom'</span><span class=\"token punctuation\">;</span>\n\n<span class=\"token keyword\">class</span> <span class=\"token class-name\">App</span> <span class=\"token keyword\">extends</span> <span class=\"token class-name\">Component</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token function\">render</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n    <span class=\"token keyword\">return</span> <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>h1</span><span class=\"token punctuation\">></span></span>Hello World<span class=\"token operator\">!</span><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>h1</span><span class=\"token punctuation\">></span></span><span class=\"token punctuation\">;</span>\n  <span class=\"token punctuation\">}</span>\n<span class=\"token punctuation\">}</span>\n\nReactDOM<span class=\"token punctuation\">.</span><span class=\"token function\">render</span><span class=\"token punctuation\">(</span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>App</span> <span class=\"token punctuation\">/></span></span><span class=\"token punctuation\">,</span> \n  document<span class=\"token punctuation\">.</span><span class=\"token function\">getElementById</span><span class=\"token punctuation\">(</span><span class=\"token string\">'root'</span><span class=\"token punctuation\">)</span>\n<span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span></code></pre>\n      </div>\n<p>Our component is just a bunch of text at the moment. If we run it through Node (or Chrome) it won't know what <code class=\"language-text\">import</code> is referring to, nor can it understand JSX syntax.</p>\n<p>This is where a tool like <a href=\"https://webpack.js.org/\">Webpack</a> comes in. It can combine all of our source files into a single bundle that can be loaded in a browser.  </p>\n<p>More importantly, if we point it at a root component, it will in turn build an internal dependency graph. Every <code class=\"language-text\">import</code> in our code will be mapped to either a <code class=\"language-text\">npm</code> package or another asset in our project (component, library, image, etc). We can use <a href=\"https://webpack.js.org/loaders/\">loaders</a> to parse additional syntax types like JSX or Sass.</p>\n<p>Let's install Webpack and give it a simple config.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-sh\"><code class=\"language-sh\">npm install --save webpack\ntouch webpack.config.js</code></pre>\n      </div>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-js\"><code class=\"language-js\"><span class=\"token comment\">/* webpack.config.js */</span>\n<span class=\"token keyword\">const</span> path <span class=\"token operator\">=</span> <span class=\"token function\">require</span><span class=\"token punctuation\">(</span><span class=\"token string\">'path'</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n\nmodule<span class=\"token punctuation\">.</span>exports <span class=\"token operator\">=</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token comment\">// Tell webpack to begin building its </span>\n  <span class=\"token comment\">// dependency graph from this file.</span>\n  entry<span class=\"token punctuation\">:</span> path<span class=\"token punctuation\">.</span><span class=\"token function\">join</span><span class=\"token punctuation\">(</span>__dirname<span class=\"token punctuation\">,</span> <span class=\"token string\">'src'</span><span class=\"token punctuation\">,</span> <span class=\"token string\">'components'</span><span class=\"token punctuation\">,</span> <span class=\"token string\">'App.js'</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">,</span>\n  <span class=\"token comment\">// And to place the output in the `build` directory</span>\n  output<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">{</span>\n    path<span class=\"token punctuation\">:</span> path<span class=\"token punctuation\">.</span><span class=\"token function\">join</span><span class=\"token punctuation\">(</span>__dirname<span class=\"token punctuation\">,</span> <span class=\"token string\">'build'</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">,</span>\n    filename<span class=\"token punctuation\">:</span> <span class=\"token string\">'bundle.js'</span>\n  <span class=\"token punctuation\">}</span>\n<span class=\"token punctuation\">}</span></code></pre>\n      </div>\n<p>Cool, now our imports will work beautifully, but we still need to figure out a way to parse JSX. For this, we need <a href=\"https://babeljs.io/\">Babel</a> â€” let's add it to our project.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-sh\"><code class=\"language-sh\"># base of the Babel ecosystem, parses JS code,\n# very powerful with the right plugins\nnpm install --save babel-core\n\n# allows Webpack to transpile JS code via Babel\nnpm install --save babel-loader\n\n# allows transpilation from latest ES2015+ \n# features to ES5\nnpm install --save babel-preset-env\n\n# and finally, JSX to ES5 transpilation\nnpm install --save babel-preset-react</code></pre>\n      </div>\n<p>Now we can update our <code class=\"language-text\">webpack.config.js</code> with a <code class=\"language-text\">module</code> field. This tells Webpack that any files with the <code class=\"language-text\">js</code> or <code class=\"language-text\">jsx</code> extensions should be parsed with Babel.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-js\"><code class=\"language-js\"><span class=\"token comment\">/* webpack.config.js */</span>\nmodule<span class=\"token punctuation\">.</span>exports <span class=\"token operator\">=</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token comment\">/* ... */</span>\n  module<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">{</span>\n    rules<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">[</span>\n      <span class=\"token punctuation\">{</span>\n        test<span class=\"token punctuation\">:</span> <span class=\"token regex\">/\\.jsx?$/</span><span class=\"token punctuation\">,</span>\n        <span class=\"token comment\">/* We'll leave npm packages as is and not \n           parse them with Babel since most of them \n           are already pre-transpiled anyway. */</span>\n        exclude<span class=\"token punctuation\">:</span> <span class=\"token regex\">/node_modules/</span><span class=\"token punctuation\">,</span>\n        use<span class=\"token punctuation\">:</span> <span class=\"token string\">'babel-loader'</span>\n      <span class=\"token punctuation\">}</span>\n    <span class=\"token punctuation\">]</span>\n  <span class=\"token punctuation\">}</span>\n<span class=\"token punctuation\">}</span></code></pre>\n      </div>\n<p>The last part of setting up Babel is telling it to use the presets we installed earlier. All you need to do is add a <code class=\"language-text\">babel</code> field to your <code class=\"language-text\">package.json</code>.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-js\"><code class=\"language-js\"><span class=\"token string\">\"babel\"</span><span class=\"token punctuation\">:</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token string\">\"presets\"</span><span class=\"token punctuation\">:</span> <span class=\"token punctuation\">[</span>\n    <span class=\"token string\">\"env\"</span><span class=\"token punctuation\">,</span>\n    <span class=\"token string\">\"react\"</span>\n  <span class=\"token punctuation\">]</span>\n<span class=\"token punctuation\">}</span></code></pre>\n      </div>\n<p>Now that that's done, we can finally compile our code into a bundle. To do this, you could run Webpack from the <code class=\"language-text\">node_modules</code> directory <code class=\"language-text\">./node_modules/.bin/webpack</code>. However, that's a bit of a pain to type out and we'll be running it <strong>a lot</strong>. Instead, we're going to add a shortcut for it in <code class=\"language-text\">package.json</code> under <code class=\"language-text\">scripts</code>.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-js\"><code class=\"language-js\"><span class=\"token string\">\"scripts\"</span><span class=\"token punctuation\">:</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token string\">\"start\"</span><span class=\"token punctuation\">:</span> <span class=\"token string\">\"webpack\"</span>\n<span class=\"token punctuation\">}</span></code></pre>\n      </div>\n<p>All that's left to do is run <code class=\"language-text\">npm run start</code>, or even better, <code class=\"language-text\">npm start</code>, and voilÃ , a success message and the compiled bundle can be found in <code class=\"language-text\">build/bundle.js</code>. </p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-sh\"><code class=\"language-sh\">Hash: b5a8448c403cf555a5de\nVersion: webpack 3.8.1\nTime: 1033ms\n    Asset    Size  Chunks                    Chunk Names\nbundle.js  729 KB       0  [emitted]  [big]  main\n  [14] ./src/App.js 2.31 KB {0} [built]\n    + 26 hidden modules</code></pre>\n      </div>\n<p>Open this file to see that it contains all of the dependencies that were imported (React and ReactDOM), as well as the source code for <code class=\"language-text\">App.js</code>... except it looks a lot different!</p>\n<p>While a bit unsightly, this is actually the result we want. As promised, Babel turned our fancy ES2015+ class into an old school constructor function and JSX tags were replaced with calls to React <a href=\"https://reactjs.org/docs/react-api.html#creating-react-elements\"><code class=\"language-text\">createElement</code></a>.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-js\"><code class=\"language-js\"><span class=\"token keyword\">var</span> <span class=\"token function-variable function\">App</span> <span class=\"token operator\">=</span> <span class=\"token keyword\">function</span> <span class=\"token punctuation\">(</span>_Component<span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token function\">_inherits</span><span class=\"token punctuation\">(</span>App<span class=\"token punctuation\">,</span> _Component<span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n\n  <span class=\"token keyword\">function</span> <span class=\"token function\">App</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n    <span class=\"token function\">_classCallCheck</span><span class=\"token punctuation\">(</span><span class=\"token keyword\">this</span><span class=\"token punctuation\">,</span> App<span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n\n    <span class=\"token keyword\">return</span> <span class=\"token function\">_possibleConstructorReturn</span><span class=\"token punctuation\">(</span><span class=\"token keyword\">this</span><span class=\"token punctuation\">,</span> <span class=\"token punctuation\">(</span>App<span class=\"token punctuation\">.</span>__proto__ <span class=\"token operator\">||</span> Object<span class=\"token punctuation\">.</span><span class=\"token function\">getPrototypeOf</span><span class=\"token punctuation\">(</span>App<span class=\"token punctuation\">)</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">.</span><span class=\"token function\">apply</span><span class=\"token punctuation\">(</span><span class=\"token keyword\">this</span><span class=\"token punctuation\">,</span> arguments<span class=\"token punctuation\">)</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n  <span class=\"token punctuation\">}</span>\n\n  <span class=\"token function\">_createClass</span><span class=\"token punctuation\">(</span>App<span class=\"token punctuation\">,</span> <span class=\"token punctuation\">[</span><span class=\"token punctuation\">{</span>\n    key<span class=\"token punctuation\">:</span> <span class=\"token string\">'render'</span><span class=\"token punctuation\">,</span>\n    value<span class=\"token punctuation\">:</span> <span class=\"token keyword\">function</span> <span class=\"token function\">render</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n      <span class=\"token keyword\">return</span> _react2<span class=\"token punctuation\">.</span><span class=\"token keyword\">default</span><span class=\"token punctuation\">.</span><span class=\"token function\">createElement</span><span class=\"token punctuation\">(</span>\n        <span class=\"token string\">'h1'</span><span class=\"token punctuation\">,</span>\n        <span class=\"token keyword\">null</span><span class=\"token punctuation\">,</span>\n        <span class=\"token string\">'Hello World!'</span>\n      <span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n    <span class=\"token punctuation\">}</span>\n  <span class=\"token punctuation\">}</span><span class=\"token punctuation\">]</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n\n  <span class=\"token keyword\">return</span> App<span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span><span class=\"token punctuation\">(</span>_react<span class=\"token punctuation\">.</span>Component<span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span></code></pre>\n      </div>\n<p>The next step is to run our code in a browser. On every build, we'll create a HTML file from a template and automatically add the relevant <code class=\"language-text\">&lt;script&gt;</code> tag pointing to the new bundle.</p>\n<p>First, let's add a basic template in <code class=\"language-text\">public/index.html</code>.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-html\"><code class=\"language-html\"><span class=\"token comment\">&lt;!-- public/index.html --></span>\n<span class=\"token doctype\">&lt;!DOCTYPE html></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>html</span> <span class=\"token attr-name\">lang</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>en<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span>\n\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>head</span><span class=\"token punctuation\">></span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>meta</span> <span class=\"token attr-name\">charset</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>UTF-8<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>meta</span> <span class=\"token attr-name\">name</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>viewport<span class=\"token punctuation\">\"</span></span> <span class=\"token attr-name\">content</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>width=device-width, initial-scale=1.0<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>meta</span> <span class=\"token attr-name\">http-equiv</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>X-UA-Compatible<span class=\"token punctuation\">\"</span></span> <span class=\"token attr-name\">content</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>ie=edge<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>title</span><span class=\"token punctuation\">></span></span>Tiny React Environment Tutorial<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>title</span><span class=\"token punctuation\">></span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>head</span><span class=\"token punctuation\">></span></span>\n\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>body</span><span class=\"token punctuation\">></span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>div</span> <span class=\"token attr-name\">id</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">\"</span>root<span class=\"token punctuation\">\"</span></span><span class=\"token punctuation\">></span></span><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>div</span><span class=\"token punctuation\">></span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>body</span><span class=\"token punctuation\">></span></span>\n\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>html</span><span class=\"token punctuation\">></span></span></code></pre>\n      </div>\n<p>Next, we'll add and configure a plugin that loads this template and outputs it at build time.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-sh\"><code class=\"language-sh\">npm install --save html-webpack-plugin</code></pre>\n      </div>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-js\"><code class=\"language-js\"><span class=\"token comment\">/* webpack.config.js */</span>\n<span class=\"token keyword\">const</span> HtmlWebpackPlugin <span class=\"token operator\">=</span> <span class=\"token function\">require</span><span class=\"token punctuation\">(</span><span class=\"token string\">'html-webpack-plugin'</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n\nmodule<span class=\"token punctuation\">.</span>exports <span class=\"token operator\">=</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token comment\">/* ... */</span>\n  plugins<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">[</span>\n    <span class=\"token keyword\">new</span> <span class=\"token class-name\">HtmlWebpackPlugin</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">{</span>\n      template<span class=\"token punctuation\">:</span> path<span class=\"token punctuation\">.</span><span class=\"token function\">join</span><span class=\"token punctuation\">(</span>__dirname<span class=\"token punctuation\">,</span> <span class=\"token string\">'public'</span><span class=\"token punctuation\">,</span> <span class=\"token string\">'index.html'</span><span class=\"token punctuation\">)</span>\n    <span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span>\n  <span class=\"token punctuation\">]</span>\n<span class=\"token punctuation\">}</span></code></pre>\n      </div>\n<p>Perfect! Now you can start a new build with <code class=\"language-text\">npm start</code> and you should see an <code class=\"language-text\">index.html</code> file in your build directory. Open it and enjoy the fruits of your labor ðŸŽ‰ </p>\n<p>If you inspect it, you'll notice that it loads <code class=\"language-text\">bundle.js</code>, even though we didn't have any <code class=\"language-text\">&lt;script&gt;</code> tags in our template â€” <code class=\"language-text\">html-webpack-plugin</code> did it for us, what a nice fellow!</p>\n<h2 id=\"adding-hot-reload\"><a href=\"#adding-hot-reload\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Adding hot reload</h2>\n<p>We've set up a way to bundle a bunch of scripts and load them in a webpage. It's not bad, but it also doesn't set the world on fire. Also, it's kind of a pain to manually run the build command every time we make a change.</p>\n<p>An easy fix for this is to tell Webpack to continue running in watch mode after finishing the build (pass <code class=\"language-text\">--watch</code> when calling it). Now, after every change to our code, the bundle will be rebuilt and we can refresh the page to see the changes.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-sh\"><code class=\"language-sh\">./node_modules/.bin/webpack --watch\n\n# Don&#39;t mind the first -- it&#39;s just a way to pass arguments to \n# scripts from package.json \nnpm start -- --watch</code></pre>\n      </div>\n<p><img src=\"/cold-reload-5012d71adbf70c13ce0224aac73045a8.gif\" alt=\"cold reload\">\n<em>Cold reloading</em></p>\n<p>Not bad. We can can make this whole process even smoother by having the page reload itself. So, instead of watch, let's use <a href=\"https://webpack.js.org/configuration/dev-server/\"><code class=\"language-text\">webpack-dev-server</code></a>.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-sh\"><code class=\"language-sh\">npm install --save webpack-dev-server</code></pre>\n      </div>\n<p>Let's change the start script so that it calls the dev server. It will now be the server's responsibility to manage Webpack and load our app.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-js\"><code class=\"language-js\"><span class=\"token string\">\"scripts\"</span><span class=\"token punctuation\">:</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token string\">\"start\"</span><span class=\"token punctuation\">:</span> <span class=\"token string\">\"webpack-dev-server --inline --hot\"</span>\n<span class=\"token punctuation\">}</span></code></pre>\n      </div>\n<p>Now, every time we make a change, <code class=\"language-text\">webpack-dev-server</code> will notify the running app that is needs to update. We're going to do a full reload when this happens.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-js\"><code class=\"language-js\"><span class=\"token comment\">/* src/components/App.js */</span>\n<span class=\"token keyword\">if</span> <span class=\"token punctuation\">(</span>module<span class=\"token punctuation\">.</span>hot<span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n  module<span class=\"token punctuation\">.</span>hot<span class=\"token punctuation\">.</span><span class=\"token function\">accept</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span></code></pre>\n      </div>\n<p>Time to fire it up and witness the magic at <a href=\"http://localhost:8080/\"><code class=\"language-text\">http://localhost:8080/</code></a> ðŸ‘Œ</p>\n<p><img src=\"/hot-reload-f454ee948c208a5c199edbedfa23fc35.gif\" alt=\"hot reload\">\n<em>Hot reloading</em></p>\n<blockquote>\n<p>For bonus points, you can make this process even smoother by using <a href=\"https://github.com/gaearon/react-hot-loader\"><code class=\"language-text\">react-hot-loader</code></a>. Instead of a full reload, it will patch only the parts that change. You components will preserve their state between updates!</p>\n</blockquote>\n<h2 id=\"adding-styles-and-assets-to-our-bundle\"><a href=\"#adding-styles-and-assets-to-our-bundle\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Adding styles and assets to our bundle</h2>\n<p>We've made good progress so far. In the current state, someone could build a decent app by just crafting a few components and integrating a database service like <a href=\"https://firebase.google.com/\">Firebase</a>. Let's give them the power to make their app shine!</p>\n<p>Webpack has been a good friend so far, so of course we'll be using it to load our styles as well. Let's first add the Sass compiler and a couple of loaders.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-sh\"><code class=\"language-sh\"># creates style tags from JS strings\nnpm install --save style-loader \n\n# translates CSS into JS strings\nnpm install --save css-loader\n\n# translates Sass into CSS\nnpm install --save sass-loader\n\n# Sass compiler used by the loader above\nnpm install --save node-sass</code></pre>\n      </div>\n<p>As you can see, multiple loaders are required here and it's actually kind of awesome that you can chain them together to create a style pipeline. To do this, just add the following rule to <code class=\"language-text\">webpack.config.js</code>.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-sh\"><code class=\"language-sh\">{\n  test: /\\.scss$/,\n  use: [&#39;style-loader&#39;, &#39;css-loader&#39;, &#39;sass-loader&#39;]\n}</code></pre>\n      </div>\n<p>Now, we can begin adding our styles. Create an <code class=\"language-text\">App.scss</code> file in <code class=\"language-text\">src/components/</code> and add some styles that will take advantage of the power that Sass provides.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-scss\"><code class=\"language-scss\"><span class=\"token property\"><span class=\"token variable\">$navy</span></span><span class=\"token punctuation\">:</span> #001e38<span class=\"token punctuation\">;</span>\n<span class=\"token property\"><span class=\"token variable\">$green</span></span><span class=\"token punctuation\">:</span> #15b78f<span class=\"token punctuation\">;</span>\n\n<span class=\"token selector\">html </span><span class=\"token punctuation\">{</span>\n  <span class=\"token property\">background-color</span><span class=\"token punctuation\">:</span> <span class=\"token variable\">$navy</span><span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span>\n\n<span class=\"token selector\">body </span><span class=\"token punctuation\">{</span>\n  <span class=\"token property\">margin</span><span class=\"token punctuation\">:</span> 0<span class=\"token punctuation\">;</span>\n  <span class=\"token property\">width</span><span class=\"token punctuation\">:</span> 100vw<span class=\"token punctuation\">;</span>\n  <span class=\"token property\">height</span><span class=\"token punctuation\">:</span> 100vh<span class=\"token punctuation\">;</span>\n  <span class=\"token property\">display</span><span class=\"token punctuation\">:</span> flex<span class=\"token punctuation\">;</span>\n  <span class=\"token property\">justify-content</span><span class=\"token punctuation\">:</span> center<span class=\"token punctuation\">;</span>\n  <span class=\"token property\">align-items</span><span class=\"token punctuation\">:</span> center<span class=\"token punctuation\">;</span>\n  <span class=\"token property\">font-family</span><span class=\"token punctuation\">:</span> monospace<span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span>\n\n<span class=\"token selector\">h1 </span><span class=\"token punctuation\">{</span>\n  <span class=\"token property\">color</span><span class=\"token punctuation\">:</span> <span class=\"token variable\">$green</span><span class=\"token punctuation\">;</span>\n  <span class=\"token property\">font-weight</span><span class=\"token punctuation\">:</span> bold<span class=\"token punctuation\">;</span>\n  <span class=\"token property\">font-size</span><span class=\"token punctuation\">:</span> 3.5rem<span class=\"token punctuation\">;</span>\n\n  <span class=\"token selector\"><span class=\"token parent important\">&amp;</span>:after </span><span class=\"token punctuation\">{</span>\n    <span class=\"token property\">content</span><span class=\"token punctuation\">:</span> <span class=\"token string\">'ðŸ‘‹'</span><span class=\"token punctuation\">;</span>\n    <span class=\"token property\">display</span><span class=\"token punctuation\">:</span> inline-block<span class=\"token punctuation\">;</span>\n    <span class=\"token property\">transform</span><span class=\"token punctuation\">:</span> <span class=\"token function\">rotate</span><span class=\"token punctuation\">(</span>-20deg<span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n    <span class=\"token property\">animation</span><span class=\"token punctuation\">:</span> wave .75s infinite linear alternate<span class=\"token punctuation\">;</span>\n  <span class=\"token punctuation\">}</span>\n<span class=\"token punctuation\">}</span>\n\n<span class=\"token atrule\"><span class=\"token rule\">@keyframes</span> wave</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token selector\">100% </span><span class=\"token punctuation\">{</span>\n    <span class=\"token property\">transform</span><span class=\"token punctuation\">:</span> <span class=\"token function\">rotate</span><span class=\"token punctuation\">(</span>20deg<span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n  <span class=\"token punctuation\">}</span>\n<span class=\"token punctuation\">}</span></code></pre>\n      </div>\n<p>In order for them to take effect, we'll need to import new newly created styles in <code class=\"language-text\">App.js</code>.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-jsx\"><code class=\"language-jsx\"><span class=\"token keyword\">import</span> <span class=\"token string\">'./App.scss'</span><span class=\"token punctuation\">;</span></code></pre>\n      </div>\n<p>Our Style Pipelineâ„¢ should be working nicely now. Even better, there's no need to worry about <code class=\"language-text\">module.hot</code> for styles because <a href=\"https://github.com/webpack-contrib/style-loader\"><code class=\"language-text\">style-loader</code></a> supports Hot Module Replacement out of the box. So cool!</p>\n<p><img src=\"/style-reload-acccef761e4105257101b874a095421b.gif\" alt=\"styles hot reload\">\n<em>Reload styles without a page refresh</em></p>\n<p>That hand emoji is nice, but it might look a bit different when viewed on another OS. Consistency is key, so we'll substitute it for an image (and learn how to load assets in the process).</p>\n<p>We don't have any loaders for image assets, so let's install some.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-text\"><code class=\"language-text\"># adds an asset to the output directory\nnpm install --save file-loader\n\n# loads small assets as dataURIs\n# large assets get handled by file-loader\nnpm install --save url-loader</code></pre>\n      </div>\n<p>These two loaders serve the same purpose â€” they allow us to reference an asset in our code and add it to the bundle. As the name implies, <code class=\"language-text\">file-loader</code> loads assets as files and <code class=\"language-text\">url-loader</code> tries to inline them as <a href=\"https://developer.mozilla.org/en-US/docs/Web/HTTP/Basics_of_HTTP/Data_URIs\">dataURIs</a>.</p>\n<p>The reason we use both instead of just relying on <code class=\"language-text\">file-loader</code> is to save on HTTP requests. We inline small assets (under 10 KB) so they'll be included in our bundle without dramatically increasing its size.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-js\"><code class=\"language-js\"><span class=\"token comment\">/* webpack.config.js */</span>\nmodule<span class=\"token punctuation\">.</span>exports <span class=\"token operator\">=</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token comment\">/* ... */</span>\n  module<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">{</span>\n    rules<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">[</span>\n      <span class=\"token comment\">/* ... */</span>\n      <span class=\"token punctuation\">{</span>\n        test<span class=\"token punctuation\">:</span> <span class=\"token regex\">/\\.(jpe?g|png|gif)$/</span><span class=\"token punctuation\">,</span>\n        use<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">[</span><span class=\"token punctuation\">{</span>\n          <span class=\"token comment\">/* inline if smaller than 10 KB, otherwise load as a file */</span>\n          loader<span class=\"token punctuation\">:</span> <span class=\"token string\">'url-loader'</span><span class=\"token punctuation\">,</span>\n          options<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">{</span>\n            limit<span class=\"token punctuation\">:</span> <span class=\"token number\">10000</span>\n          <span class=\"token punctuation\">}</span>\n        <span class=\"token punctuation\">}</span><span class=\"token punctuation\">]</span>\n      <span class=\"token punctuation\">}</span><span class=\"token punctuation\">,</span>\n      <span class=\"token punctuation\">{</span> \n        test<span class=\"token punctuation\">:</span> <span class=\"token regex\">/\\.(eot|svg|ttf|woff2?|otf)$/</span><span class=\"token punctuation\">,</span>\n        use<span class=\"token punctuation\">:</span> <span class=\"token string\">'file-loader'</span>\n      <span class=\"token punctuation\">}</span>\n    <span class=\"token punctuation\">]</span>\n  <span class=\"token punctuation\">}</span>\n<span class=\"token punctuation\">}</span><span class=\"token punctuation\">;</span></code></pre>\n      </div>\n<p>If we add an image in <code class=\"language-text\">src/assets</code>, it should now be possible to reference it in <code class=\"language-text\">App.scss</code>...</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-scss\"><code class=\"language-scss\"><span class=\"token selector\">h1 </span><span class=\"token punctuation\">{</span>\n  <span class=\"token selector\"><span class=\"token parent important\">&amp;</span>:after </span><span class=\"token punctuation\">{</span>\n    <span class=\"token property\">content</span><span class=\"token punctuation\">:</span> <span class=\"token string\">''</span><span class=\"token punctuation\">;</span>\n    <span class=\"token property\">display</span><span class=\"token punctuation\">:</span> inline-block<span class=\"token punctuation\">;</span>\n    <span class=\"token property\">width</span><span class=\"token punctuation\">:</span> 1em<span class=\"token punctuation\">;</span>\n    <span class=\"token property\">height</span><span class=\"token punctuation\">:</span> 1em<span class=\"token punctuation\">;</span>\n    <span class=\"token property\">background</span><span class=\"token punctuation\">:</span> <span class=\"token url\">url</span><span class=\"token punctuation\">(</span><span class=\"token string\">'../assets/waving-hand-sign.png'</span><span class=\"token punctuation\">)</span>\n                center/100%\n                no-repeat<span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span></code></pre>\n      </div>\n<p>...or in <code class=\"language-text\">App.js</code>.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-jsx\"><code class=\"language-jsx\"><span class=\"token keyword\">import</span> handWave <span class=\"token keyword\">from</span> <span class=\"token string\">'../assets/waving-hand-sign.png'</span><span class=\"token punctuation\">;</span>\n\n<span class=\"token keyword\">class</span> <span class=\"token class-name\">App</span> <span class=\"token keyword\">extends</span> <span class=\"token class-name\">Component</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token function\">render</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n    <span class=\"token keyword\">return</span> <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>h1</span><span class=\"token punctuation\">></span></span>\n      Hello World<span class=\"token operator\">!</span>\n      <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>img</span> <span class=\"token attr-name\">src</span><span class=\"token script language-javascript\"><span class=\"token script-punctuation punctuation\">=</span><span class=\"token punctuation\">{</span>handWave<span class=\"token punctuation\">}</span></span> <span class=\"token punctuation\">/></span></span>\n    <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>h1</span><span class=\"token punctuation\">></span></span><span class=\"token punctuation\">;</span>\n  <span class=\"token punctuation\">}</span>\n<span class=\"token punctuation\">}</span></code></pre>\n      </div>\n<h2 id=\"ship-it\"><a href=\"#ship-it\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Ship it!</h2>\n<p>The last thing we'll do today is deploy our app. Surge makes this process super easy, so let's add it to our project.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-text\"><code class=\"language-text\">npm install --save surge</code></pre>\n      </div>\n<p>All that's left is to add a script to our <code class=\"language-text\">package.json</code> that will build the bundle and call the Surge CLI to upload it.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-js\"><code class=\"language-js\"><span class=\"token string\">\"scripts\"</span><span class=\"token punctuation\">:</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token string\">\"deploy\"</span><span class=\"token punctuation\">:</span> <span class=\"token string\">\"webpack &amp;&amp; surge -p ./build/ -d tiny-react-env.surge.sh\"</span>\n<span class=\"token punctuation\">}</span></code></pre>\n      </div>\n<p>Woohoo! We're now up and running at <a href=\"http://tiny-react-env.surge.sh/\">http://tiny-react-env.surge.sh/</a>. Good job!</p>\n<h2 id=\"whats-next\"><a href=\"#whats-next\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>What's next?</h2>\n<p>It wasn't easy but we've managed to set up a workspace for our future project. Let's do a quick recap:</p>\n<ul>\n<li>Our code is transpiled with Babel and we can use the latest ES2015+ features.</li>\n<li>We used Webpack to build our bundle and Webpack Dev Server to develop locally with hot reloading.</li>\n<li>We have a style pipeline that compiles our Sass code and we're also bundling our assets.</li>\n</ul>\n<p>This is just the tip of the iceberg though â€” there is a lot more you can do to expand this setup. Here are some ideas for what to try next:</p>\n<ol>\n<li>Enable source maps by setting a <a href=\"https://webpack.js.org/configuration/devtool/\"><code class=\"language-text\">devtool</code></a> field in the webpack config.</li>\n<li>Create a separate <strong>production</strong> config to be used when deploying, disable source maps for it, and minify the bundle with <a href=\"https://webpack.js.org/plugins/babel-minify-webpack-plugin/\"><code class=\"language-text\">babel-minify-webpack-plugin</code></a>.</li>\n<li>Use <a href=\"https://github.com/webpack-contrib/extract-text-webpack-plugin\"><code class=\"language-text\">extract-text-webpack-plugin</code></a> to output your style bundle as a separate file from the JS bundle.</li>\n</ol>\n<p>Good luck!</p>\n<blockquote>\n<p>This article was originally published on <a href=\"https://www.codementor.io/valentinrad/roll-your-own-tiny-react-environment-using-webpack-babel-and-sass-ei70wyhjl\">Codementor</a>.</p>\n</blockquote>","frontmatter":{"title":"Roll your own tiny React environment using Webpack, Babel and Sass"}}},"pathContext":{"slug":"/posts/tiny-react-env/"}}

/***/ })

});


